import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { formatterCurrency } from "../../../utils/string";
import { connect, useDispatch, useSelector } from "react-redux";
import addToCartCheck from "./addToCartCheck";
import Loader from "../../../components/features/loaders/loader";
import MediaImage from "./MediaImage";
import { getVariation } from "../../../api/products";
import { setChoosenProduct } from "../../../actions/choosenProducts";
import market from "../../../config/market";
import { dt } from "../../../i18n";

function CompareProductCard(props) {
	const { cart } = useSelector((state) => state.cartlist);

	const { product, onAddToCart, deleteProduct, haveSupplier = false } = props;

	const { token } = useSelector((state) => state.user.tokenData);

	const dispatch = useDispatch();

	const [loading, setLoading] = useState(false);

	const [setSupplier] = useState();

	const { productList } = useSelector((state) => state.choosenProducts);
	const { productListCompare } = useSelector(
		(state) => state.comparisonProducts
	);

	const haveChoosen = productList.find(
		(p) => p.stock.id === product?.stock.id
	);

	const haveCompare = productListCompare.find(
		(p) => p?.stock?.id === product?.stock?.id
	);

	const haveCart = cart.find((p) => p.data.stock.id === product?.stock.id);

	useEffect(() => {
		if (haveSupplier && props.suppliers) {
			const supplier = props.suppliers.find(
				(s) => s.id === product?.stock.supplier
			);
			setSupplier(supplier);
		}
	}, [haveSupplier, props.suppliers]);

	const addToCartHandler = () => {
		setLoading(true);
		getVariation(product.productId)
			.then((dataProduct) => {
				if (dataProduct.variations.length === 1) {
					addToCartCheck(
						dataProduct,
						1,
						token,
						setLoading,
						dispatch,
						cart
					);
				} else {
					onAddToCart(dataProduct, 1);
					setLoading(false);
				}
			})
			.catch((err) => {
				console.error(err);
			});
	};
	const images = product?.variation?.images[0];
	return product ? (
		<div className="compare-card-one">
			<div className="product-header">
				{product.discount > 0 ? (
					<Link
						to={`/products/${product.variation.id}`}
						className="discount"
					>
						<span className="text">
							-
							{(
								100 -
								(product.newPrice * 100) / product.oldPrice
							).toFixed(0)}
							%
						</span>
					</Link>
				) : null}
				<div className="header-buttons">
					{market.config.compare ? (
						<button
							className={`${haveCompare ? `have-compare` : ``}`}
							onClick={() => {
								deleteProduct(product);
							}}
						>
							<img
								src={market.config.icons.deleteIcon}
								alt="compare"
							/>
						</button>
					) : null}
					<button
						className={`${haveChoosen ? `have-choosen` : ``}`}
						onClick={() => {
							dispatch(setChoosenProduct(product));
						}}
					>
						<img
							src={market.config.icons.favourites}
							alt="favourites"
						/>
					</button>
					{loading ? (
						<button className="cart-btn">
							<Loader />
						</button>
					) : (
						<button
							className={`${haveCart ? `have-cart` : ``}`}
							onClick={addToCartHandler}
						>
							<img src={market.config.icons.cart} alt="cart" />
						</button>
					)}
				</div>

				<Link to={`/product/${product.variation.id}`} className="w-100">
					<MediaImage
						image={images}
						largeSize={"300x_"}
						mediumSize={"150x_"}
						smallSize={"150x_"}
						alt={product.name}
					/>
				</Link>
			</div>
			<div className="body-product">
				<h6 className="title-product">
					<Link to={"/product/" + product.variation.id}>
						{dt(product.variation.name)}
					</Link>
				</h6>
				<div className="price-product">
					<span className="price-new">
						{formatterCurrency(product.newPrice)}
					</span>
					{product.discount ? (
						<span className="price-old">
							<del>{formatterCurrency(product.oldPrice)}</del>
						</span>
					) : null}
				</div>
			</div>
		</div>
	) : null;
}

export default connect((state) => ({ settings: state.marketData.settings }))(
	CompareProductCard
);
