import { compareProductTypes } from "../constants/action-types";

export const setCompareProducts = (product) => (dispatch) => {
	console.log(product);
	var newProduct = {
		newPrice: product.newPrice,
		oldPrice: product.oldPrice,
		stock: { id: product.stock.id },
		productId: product.productId || product.variation.product,
		variation: {
			name: product.variation.name,
			productProperties: product.variation.productProperties.filter(
				(product) => product.value.length > 0
			),
			images: product.variation.images,
			id: product.variation.id,
		},
	};
	dispatch({
		type: compareProductTypes.ADD_TO_COMPARE,
		newProduct,
	});
};

export const removeFromCompare = (product) => (dispatch) => {
	var newProduct = product;
	dispatch({
		type: compareProductTypes.REMOVE_FROM_COMPARE,
		newProduct,
	});
};

export const removeAllFromCompare = () => (dispatch) => {
	dispatch({
		type: compareProductTypes.RESET_COMPARE,
	});
};
