import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { addToCart } from "../../actions/cart";
import {
	removeAllFromCompare,
	removeFromCompare,
} from "../../actions/comparisonProducts";
import { getProductCategoryPaths } from "../../api/products";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import CompareProductCard from "../../components/features/product/CompareProductCard";
import market from "../../config/market";
import { makeLoopId, toTop } from "../../utils/oxMethods";

export default function CompareProductsOne() {
	const [active, setActive] = useState();
	const [menus, setMenus] = useState();
	const [loading, setLoading] = useState(true);
	const [properties, setProperties] = useState([]);
	const dispatch = useDispatch();
	const { productListCompare } = useSelector(
		(state) => state.comparisonProducts
	);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 0 } },
	});

	const scroll = window.addEventListener("scroll", () => {
		const div = document.querySelector(".compare-product-list");
		const header = document.querySelector(".sticky-top");

		if (div && header) {
			if (window.scrollY > 140) {
				div.classList.add("fixed");
				header.classList.add("fixed");
			} else {
				div.classList.remove("fixed");
				header.classList.remove("fixed");
			}
		}
	});

	async function fetchData() {
		await getProductCategoryPaths(makeLoopId(productListCompare)).then(
			(res) => {
				const menus = res.reduce((newMenu, menu) => {
					const m = Object.values(menu)[0][0];
					if (!m) {
						dispatch(
							removeFromCompare(
								productListCompare.find(
									(product) =>
										product.productId ===
										parseInt(Object.keys(menu)[0])
								)
							)
						);
						return newMenu;
					}
					var menuIndex = newMenu.findIndex((nm) => nm.id === m?.id);
					if (menuIndex < 0) {
						newMenu.push({
							...m,
							productIds: [Object.keys(menu)[0]],
						});
					} else {
						newMenu[menuIndex].productIds.push(
							Object.keys(menu)[0]
						);
					}
					return newMenu;
				}, []);
				setMenus(menus);
				setLoading(false);
			}
		);
	}
	useEffect(() => {
		if (makeLoopId(productListCompare)) {
			fetchData();
		} else {
			setLoading(false);
		}
		return () => {
			window.removeEventListener("scroll", scroll);
		};
	}, [makeLoopId]);
	useEffect(() => {
		if (menus) {
			setActive(menus[0]);
			toTop();
		}
	}, [menus, setMenus]);

	async function deleteMenuProducts(menu) {
		await menu.productIds.forEach((id) => {
			dispatch(
				removeFromCompare(
					productListCompare.find(
						(product) => product.productId === parseInt(id)
					)
				)
			);
		});
		setMenus(menus.filter((item) => item.id !== menu.id));
	}
	function deleteProduct(product) {
		const ids = active.productIds.filter(
			(id) => parseInt(id) !== product.productId
		);
		dispatch(removeFromCompare(product));
		setActive({
			...active,
			productIds: ids,
		});
	}
	function deleteAllMenus() {
		dispatch(removeAllFromCompare());
		if (makeLoopId(productListCompare)) {
			setProperties([]);
			setMenus([]);
		}
	}

	function onAddToCart(dataProduct) {
		setModal({
			open: true,
			product: dataProduct,
		});
	}
	useEffect(() => {
		if (active) {
			const properties = [];
			active.productIds.forEach((id) => {
				const nextProperty = productListCompare.find(
					(product) => product?.productId === parseInt(id)
				)?.variation.productProperties;
				if (nextProperty) {
					properties.push(...nextProperty);
				}
			});

			setProperties(properties);

			if (active.productIds.length < 1) {
				setMenus(menus.filter((item) => item.id !== active.id));
			}
		}
	}, [active]);
	const { t } = useTranslation();
	return loading ? (
		<LoaderOverlay />
	) : (
		<div className="compare-product-list">
			<div className="container compare-product-list_body">
				<div className="compare-product-list_header mt-3">
					<h3>{t("compare-products")}</h3>
				</div>
				{productListCompare.length > 0 ? (
					<>
						<div className="tablist-clear">
							<ul className="tab-list">
								{menus?.map((menu) => (
									<li
										className={`tab-item ${
											menu.id === active.id
												? `active`
												: ``
										}`}
										key={menu.id}
									>
										<span onClick={() => setActive(menu)}>
											{menu.name}
										</span>
										<button
											className="btn-transparent"
											onClick={() =>
												deleteMenuProducts(menu)
											}
										>
											<img
												src={market.config.icons.closer}
												alt="delete"
											/>
										</button>
									</li>
								))}
								<li className="tab-item only-mobile">
									<span
										className="clear-all"
										onClick={deleteAllMenus}
									>
										{t("clear-all")}
									</span>
								</li>
							</ul>
							<button
								className="clear-all only-desktop"
								onClick={deleteAllMenus}
							>
								{t("clear-all")}
							</button>
						</div>
						<div className="row p-0 m-0 mt-2 sticky-top">
							<div className="container d-flex">
								{productListCompare.length > 0
									? active?.productIds
											.slice(0, 4)
											.map((id) => {
												const nextProduct = productListCompare.find(
													(product) =>
														product.productId ===
														parseInt(id)
												);
												return (
													<div
														key={nextProduct?.id}
														className="col-lg-3 col-md-4 col-6"
													>
														<CompareProductCard
															onAddToCart={(
																dataProduct
															) => {
																onAddToCart(
																	dataProduct
																);
															}}
															product={
																nextProduct
															}
															deleteProduct={
																deleteProduct
															}
														/>
													</div>
												);
											})
									: null}
							</div>
						</div>
						<div className="compare-property-list_header mt-3">
							<h3>{t("all-characteristics")}</h3>
						</div>
						<div className="row">
							{active?.productIds.slice(0, 4).map((id) => {
								const nextProduct = productListCompare.find(
									(product) =>
										product.productId === parseInt(id)
								);
								return (
									<div
										key={nextProduct?.id}
										className="col-lg-3 col-md-4 col-6"
									>
										<ul>
											{properties?.map((item, i) => (
												<li key={i}>
													<h4>{item.name}</h4>
													<p>
														{nextProduct?.variation?.productProperties.find(
															(property) =>
																property.id ===
																item.id
														)?.value || (
															<span>
																{t(
																	"нет данных"
																)}
															</span>
														)}
													</p>
												</li>
											))}
										</ul>
									</div>
								);
							})}
						</div>
					</>
				) : (
					<h3 className="empty">{t("empty")}</h3>
				)}
			</div>
			<ChooseVariationModal
				setModal={setModal}
				modal={modal}
				addToCart={addToCart}
			/>
		</div>
	);
}
