import { toast } from "react-toastify";
import { addToCart } from "../../../actions/cart";
import { addToCartApi } from "../../../api/cart";
import { getPriceDiscount } from "../../../utils/oxMethods";
import i18n from "../../../i18n";

export default function addToCartCheck(
	product,
	count,
	token,
	setLoading,
	dispatch,
	cart
) {
	setLoading(true);
	if (token) {
		addToCartApi(count, product.data.stock.id, token)
			.then(res => {
				dispatch(addToCart(product, count, res[0].id));
				window.toast({ product: product, message: "success" });
				setLoading(false);
			})
			.catch(e => {
				if (e.response?.status === 401) {
					addToCartCheck(
						product,
						count,
						null,
						setLoading,
						dispatch,
						cart
					);
				} else {
					toast.error(e.response?.data.message || e.response.data, {
						position: "top-left"
					});
				}
				setLoading(false);
			});
	} else {
		let oldProduct = cart.find(
			cart => cart.data.variation.id === product.data.variation.id
		);

		if (oldProduct) {
			if (
				product.data.stock.count -
					(parseInt(oldProduct.count) + count) <
				0
			) {
				toast.error(i18n.t("amount-balance"), {
					position: "top-left"
				});
				setLoading(false);
			} else {
				oldProduct.count = parseInt(oldProduct.count) + count;
				oldProduct.sum =
					oldProduct.count * getPriceDiscount(oldProduct.data);
				dispatch(addToCart(oldProduct, oldProduct.count));

				window.toast({ product: product, message: "success" });

				setLoading(false);
			}
		} else {
			dispatch(addToCart(product, count));
			window.toast({ product: product, message: "success" });

			setLoading(false);
		}
	}
}
