import React, { useEffect, useState } from "react";
import Modal from "react-modal";
import PropertiesView from "../../../views/product/partials/media/components/PropertiesView";
import { getVariation } from "../../../api/products";
import { useDispatch, useSelector } from "react-redux";
import { unstable_batchedUpdates } from "react-dom";
import addToCartCheck from "../product/addToCartCheck";
import Loader from "../loaders/loader";
import { useTranslation } from "react-i18next";
const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)",
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000",
	},
};

Modal.setAppElement("#root");

function ChooseVariationModal({ setModal, modal }) {
	let { product } = modal;
	const { cart } = useSelector((state) => state.cartlist);
	const dispatch = useDispatch();
	const { token } = useSelector((state) => state.user.tokenData);
	const [enable, setEnable] = useState(true);
	const [loading, setLoading] = useState(false);
	const [varId, setVarId] = useState(0);
	const { t } = useTranslation();
	let timer;

	useEffect(() => {
		if (loading) {
			setEnable(false);
		}
	}, [loading]);

	function closeModal() {
		setModal({
			product: { variation: { id: 0 } },
			open: false,
		});
		setEnable(true);
	}

	useEffect(() => {
		if (modal.open) {
			setVarId(product.data.variation.id);
		}
		return () => {
			if (timer) clearTimeout(timer);
		};
	});

	function onComplated(variationId) {
		if (variationId > 0) {
			unstable_batchedUpdates(() => {
				setVarId(variationId);
				setEnable(true);
			});
		} else {
			setEnable(false);
		}
	}

	function addToCartHandler() {
		if (varId > 0) {
			setLoading(true);
			getVariation(varId).then((product) => {
				addToCartCheck(product, 1, token, setLoading, dispatch, cart);
				if (!loading) {
					closeModal();
				}
			});
		}
	}

	return (
		<>
			<Modal
				isOpen={modal.open}
				onRequestClose={closeModal}
				style={customStyles}
				contentLabel="Choose Variation"
				className="modal-dialog"
				id="choosevariation"
			>
				<div className="modal-content">
					<div className="modal-body modal-variation">
						<div className="product-name d-none">
							<div className="title">
								<span>
									{product?.data?.variation?.productName}
								</span>
							</div>
							<span className="gradient-div d-none" />
						</div>
						<button
							type="button"
							className="close"
							data-dismiss="modal"
							aria-label="Close"
							onClick={closeModal}
						>
							<span aria-hidden="true">
								<i className="icon-close"></i>
							</span>
						</button>
						<div>
							<div className="product-details product-details-centered">
								{modal.open && (
									<PropertiesView
										product={product}
										onComplated={onComplated}
										selectVar={false}
										key={product?.product?.id || 5}
									/>
								)}
								<button
									className="btn-product btn-cart btn-variation"
									disabled={!enable}
									onClick={addToCartHandler}
								>
									{loading ? (
										<Loader />
									) : (
										<span>{t("add-cart")}</span>
									)}
								</button>
							</div>
						</div>
					</div>
				</div>
			</Modal>
		</>
	);
}

export default ChooseVariationModal;
