import React, { useMemo, useState } from "react";
import { useSelector } from "react-redux";
import market from "../../../../../config/market";
import { dt } from "../../../../../i18n";
import { getImageUrl, removeProps } from "../../../../../utils/oxMethods";
import {
	isCompleted,
	setChoosenProp,
	setAllowProps,
	getChoosenVariation,
	getCurrentVarPropsIds,
	generatePropertiesRow
} from "./propViewHelpers";

function getImageIfColorRowButNotColorOrValue(
	variations,
	property,
	colorRow,
	rowName
) {
	const variation = variations.find(
		v => v.properties[rowName] === property.value
	);
	if (colorRow) {
		return getImageUrl(variation?.image, "150x_");
	}
}

function PropertiesView({ product, onComplated, selectVar }) {
	const { settings } = useSelector(state => state.marketData);
	const [render, setRender] = useState(false);
	// console.log(product);
	const propertiesRow = useMemo(() => {
		return generatePropertiesRow(product, selectVar);
	}, [product.data.variation.id]);

	let propertiesRowSort = [
		propertiesRow.find(prop => prop.name === "Цвет"),
		...propertiesRow.filter(prop => prop.name !== "Цвет")
	];

	if (settings && settings.variationProperty) {
		propertiesRowSort = removeProps(
			propertiesRowSort,
			settings.variationProperty
		);
	}

	const [choosenProps] = useState(
		getCurrentVarPropsIds(
			product.data.variation.properties,
			propertiesRowSort
		)
	);

	if (choosenProps) {
		choosenProps.forEach(prop => (prop ? (prop.selected = true) : ""));
	}

	function onClickVariation(rowIndex, selectedProp) {
		let { variations } = product;

		setChoosenProp(propertiesRowSort, rowIndex, choosenProps, selectedProp);
		setAllowProps(propertiesRowSort, rowIndex, selectedProp, variations);

		if (isCompleted(choosenProps) && selectedProp.selected) {
			let selectedProps = {};
			choosenProps.forEach(prop => {
				selectedProps[prop.name] = prop.value;
			});
			for (let variation of variations) {
				if (getChoosenVariation(selectedProps, variation.properties)) {
					onComplated(variation.id);
					setRender(!render);
					return;
				}
			}
			onComplated(0);
		} else {
			onComplated(0);
		}
		setRender(!render);
	}

	return propertiesRowSort.map((row, rowIndex) => {
		let { properties, name } = row;
		let colorRow = name === "Цвет";

		if (properties.some(prop => prop.value === "-")) {
			return "";
		}
		// console.log(properties);

		return (
			<div key={rowIndex} className="details-filter-row details-row-size">
				<div className="gradient-div d-none" />
				<label>{dt(name)}:</label>
				<div
					className={`product-nav ${
						market.config.home === 6 ? `col-md-7 px-0` : ``
					}`}
				>
					{properties.map(prop => {
						const type = {
							block: prop.settings ? false : true,
							dot: prop.settings
								? prop.settings.type === "color"
								: false
						};
						let className = "";
						if (prop.selected) className = "active";
						if (!prop.allowed) className = "not-allowed";
						if (prop.selected && !prop.allowed) {
							className = "not-allowed active-not-allowed";
						}

						const style = {};
						if (type.block || colorRow) {
							className += ` properties-block ${
								name === "Цвет"
									? `color-properties`
									: `size-properties`
							}`;
						} else if (type.dot) {
							prop.value = "";
							className += " product-nav-dots";
							style.backgroundColor = prop.settings.color
								? prop.settings.color
								: prop.settings.property_value;
						}

						let image = getImageIfColorRowButNotColorOrValue(
							product.variations,
							prop,
							colorRow,
							name
						);
						if (prop.value === "-") {
							return "";
						}

						return (
							<div
								key={prop.id}
								className={className}
								style={{
									...style,
									position: "relative",
									cursor: "pointer",
									display: "flex",
									alignItems: "center",
									justifyContent: "center"
								}}
								onClick={() => {
									onClickVariation(rowIndex, prop);
								}}
							>
								<div className="tooltip-property">
									<span>{prop.value}</span>
								</div>

								{image ? (
									<img
										style={{
											display: "block",
											width: "100%",
											objectFit: "cover",
											borderRadius: "5px"
										}}
										src={
											image === "#" || !image
												? `/assets/images/wooz/noimg.jpg`
												: image
										}
										alt={product.data.variation.name}
										width="50"
										height="60"
									/>
								) : prop.value ? (
									<span>{prop.value}</span>
								) : (
									"-"
								)}
							</div>
						);
					})}
				</div>
			</div>
		);
	});
}

export default React.memo(
	PropertiesView,
	(pp, np) => pp.product.product.id === np.product.product.id
);
