import { getPropSettings } from "../../../../../utils/oxMethods";
const { groupBy } = require("../../../../../utils/array");
const { sequencePropertySize } = require("../../../../../utils/string");

function isEqualProps(currentProp, variationsObj) {
	for (const key in currentProp) {
		if (Object.hasOwnProperty.call(currentProp, key)) {
			const value = currentProp[key];
			const variationValue = variationsObj[key]
				? variationsObj[key]
				: "-";
			if (variationValue !== value) {
				return false;
			}
		}
	}
	return true;
}

export const setChoosenProp = (
	propertiesRow,
	rowIndex,
	choosenProps,
	selectedProp
) => {
	propertiesRow[rowIndex].properties.forEach(prop => {
		if (prop.id === selectedProp.id) {
			prop.selected = true;
			prop.allowed = true;
			choosenProps[rowIndex] = selectedProp;
		} else {
			prop.allowed = true;
			prop.selected = false;
		}
	});
};

export const setAllowProps = (
	propertiesRow,
	rowIndex,
	selectedProp,
	variations
) => {
	propertiesRow.forEach(({ name, properties }, index) => {
		if (index !== rowIndex) {
			properties.forEach(prop => {
				let currentProp = {
					[name]: prop.value
						? prop.value
						: prop.settings.property_value,
					[selectedProp.name]: selectedProp.value
						? selectedProp.value
						: selectedProp.settings.property_value
				};
				for (const variation of variations) {
					if (isEqualProps(currentProp, variation.properties)) {
						prop.allowed = true;
						return;
					}
				}
				prop.allowed = false;
				prop.selected = false;
			});
		}
	});
};

export const isCompleted = choosenProps => {
	for (const prop of choosenProps) {
		if (!prop) {
			return false;
		}
	}
	return true;
};

export function getCurrentVarPropsIds(variationProps, propertiesRow) {
	return propertiesRow.map(row =>
		row.properties.find(prop =>
			variationProps.find(
				vprop => prop.value === (vprop.value ? vprop.value : "-")
			)
		)
	);
}

export const getChoosenVariation = (selectedProps, variationObj) => {
	for (const key in selectedProps) {
		if (Object.hasOwnProperty.call(selectedProps, key)) {
			const value = selectedProps[key];
			let variationValue = variationObj[key] ? variationObj[key] : "-";
			if (variationValue !== value) {
				return false;
			}
		}
	}
	return true;
};

export const generatePropertiesRow = product => {
	const propertiesRow = [];
	for (let i in product.properties) {
		propertiesRow.push(product.properties[i]);
	}
	let properties = Array.from(groupBy(propertiesRow, prop => prop.name));

	let propertiesFix = [];
	properties.forEach((props, rowIndex) => {
		if (props[0] === "Размер") {
			props[1] = sequencePropertySize(props[1]);
		}
		props[1].forEach((prop, propIndex) => {
			if (
				product.variations.find(
					variation => variation.properties[props[0]] === prop.value
				)
			) {
				propertiesFix.push({
					...prop,
					id: (rowIndex + 1) * 10 + propIndex + 1,
					selected: false,
					allowed: true,
					value: prop.value ? prop.value : "-",
					settings: getPropSettings(
						{
							name: prop.name,
							value: prop.value
						},
						product.properties
					)
				});
			}
		});
	});

	properties = Array.from(groupBy(propertiesFix, prop => prop.name));

	return properties.map(props => {
		return {
			name: props[0],
			properties: props[1]
		};
	});
};
